.carousel {
    position: relative;
    canvas {
        //display: none;
    }
    .main-slides {
        position: relative;
        outline: none;

        .flickity-viewport {
            overflow: hidden;
        }
        //overflow: hidden;

        .carousel__content {
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            margin-bottom: 20px;
            align-items: center;
            &-image {
                display: block;
                //background-color: grey;
                width: 100%;
                position: relative;
                //height: 0;
        
                @include bp(small) {
                    flex: 1;
                    .carousel__content-image + & {
                        margin-left: 2.5%;
                     }         
                }
                
         
            }
            &-text {
                width: 100%;
                @include bp(small) {
                    padding: 5%;
                    width: 33%;
                    min-width: 300px;           
                }
        
                a {
                    display: inline-block;
                    padding-top: 10px;
                }
            }
        }
    }
    .nav-slides {

        width: 100%;
        outline: none;
        display: block;
        position: relative;
        margin-top: 20px;

        .slide {

            display: block;
            //color: white;
            width: 100%;
            margin: 0px 10px;
            cursor: pointer;

            font: inherit;
            background: initial;
            color: inherit;
            padding: 0;
            border: none;
            text-align: left;

            @include bp(small) {
                width: 50%;
            }

            @include bp(medium) {
                width: 33.33%;
            }

            h3 {
                display: inline;
            }

            &.is-selected {
                h3 {
                    //border-bottom: 1px solid black;
                    box-shadow:
                      inset 0 -0px white,
                      inset 0 -1px black;
                }
            }
        }
    }

    .flickity-button {
        position: absolute;
        top: 50%;
        height: 45px;
        width: 45px;
        border-radius: 50%;
        background-color: rgba(grey, .3);
        border: none;
        padding: 11px;
        fill: white;
        cursor: pointer;

        &.previous {
            left: -7%;
        }
        &.next {
            right: -7%;
        }

        &:disabled {
            fill: grey;
            cursor: default;
        }

        .basic-page & {
            background-color: white;
            border: 1px solid black;
            fill: black;

            &:disabled {
                background-color: black;
                fill: white;
            }
        }

    }

    .flickity-page-dots {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
        padding: 0;
        cursor: pointer;
        li {
            display: block;
            height: 10px;
            width: 10px;
            margin: 0 5px;
            background-color: black;
            border-radius: 50%;
            

            &.is-selected {
                border: 1px solid black;
                background-color: white;
            }
        }
    }

}